<template>
  <section id="bubbles"></section>
</template>

<script>
  import 'particles.js/particles';
  const particlesJS = window.particlesJS;

  export default {
    name: 'Bubbles',
    props: {
        
    },
    mounted: function() {
      this.initParticles();
    },  
    methods: {
